.company__main-content {
	padding-bottom: 94px;
	@include rmin(992) {
		padding-top: 215px; }
	@include r(991) {
		padding-top: 280px; }
	@include r(549) {
		padding-top: 65px; }
	@include r(767) {
		padding-bottom: 80px; } }
.company__h1-block {
	@include r(549) {
		padding-bottom: 0; }
	.h1-block__container {
		&>div {
			color: $gray-light;
			font: 300 22px "Hind", sans-serif;
			line-height: 1.3em;
			@include r(991) {
				top: 120px; }
			@include r(767) {
				font-size: 18px;
				top: 66px; }
			&:after {
				content: '';
				position: absolute;
				bottom: -40px;
				left: 50%;
				transform: translateX(-50%);
				height: 2px;
				width: 65px;
				background: $gray; }
			strong {
				font-weight: 600; }
			p {
				color: $blue-light;
				font-weight: 400;
				margin-top: 13px;
				font-size: 15px;
				span {
					font-weight: 700; } } } } }
.column {
	display: inline-block;
	width: 353px;
	font: $main-font;
	color: $gray-light;
	vertical-align: top;
	text-align: justify;
	line-height: 1.1em;
	@include r(991) {
		width: 300px;
		img {
			 width: 300px;
			 height: auto; } }
	@include r(767) {
		font:	$small-main-font;
		max-width: 360px;
		width: auto;
		img {
			width: 360px; } }
	@include r(549) {
		img {
			width: 280px;
			display: block;
			margin-left: auto;
			margin-right: auto; } } }
.two-col-block {
	text-align: center;
	.column:first-of-type {
		margin-right: 65px;
		@include r(991) {
			margin-right: 30px; }
		@include r(767) {
			margin-right: 0; }
		&:first-letter {
			font-size: 4em;
			font-weight: 700;
			float: left; } } }
.company__left-column {
	img {
		margin-top: 35px;
		@include r(768) {
			margin-bottom: 30px; } } }
.company__right-column {
	img {
		margin-bottom: 35px;
		@include r(768) {
			margin-bottom: 0;
			margin-top: 35px; } } }
.company__left-column, .company__right-column {
	img {
		display: block;
		margin-left: auto;
		margin-right: auto;
		max-width: 353px;
		max-height: 265px; } }
